@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
html,body{box-sizing:border-box;height:100vh;font-family:"Fjalla One", sans-serif;font-size:62.5%;display:flex;justify-content:center;align-items:center;background-color:#2d2a32}.fade{-webkit-animation-name:fade;animation-name:fade;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes fade{0%{opacity:1}25%{opacity:0}100%{opacity:1}}@keyframes fade{0%{opacity:1}25%{opacity:0}100%{opacity:1}}

#button_welcome{height:35rem;width:35rem;font-size:5rem;text-transform:uppercase;background-color:#ddd92a;border:3px solid transparent;border-radius:50%;box-shadow:0px 0px 50px #eeefa8;cursor:pointer;transition:2s ease-in-out;outline:none;-webkit-appearance:initial}#button_welcome:focus{outline:none;-webkit-appearance:initial}

#quote-box{width:55rem;background-color:#fff;padding-bottom:3rem}#quote-box #text::before{content:"“";display:block;font-size:3.5rem;font-weight:bold}#quote-box #text{font-size:3.5rem;padding-top:2rem;margin:0 1.5rem}#quote-box #text::after{content:" ”";font-size:3.5rem;font-weight:bold}#quote-box #author::before{content:"- "}#quote-box #author{margin:2em 1.5rem;font-size:2em;font-weight:bold}#quote-box-buttons{display:flex;justify-content:space-around}#quote-box-buttons button{padding:10px 25px;font-size:2rem;border-radius:5px;border:none;cursor:pointer;color:#fff;outline:none}#quote-box-buttons--twitter{background-color:#1da1f2}#quote-box-buttons--next{background-color:#2d2a32}.animate__animated.animate__zoomInUp{-webkit-animation-duration:0.6s;animation-duration:0.6s}@media only screen and (max-width: 600px){#quote-box{width:90%;margin:0 auto}#quote-box #text{font-size:3rem}#quote-box #author{font-size:1.25rem}#quote-box button{padding:5px 15px;font-size:1.5rem}}

